<template>
    <div>
        <h1>影像部</h1>
        <h2>{{this.$route.params.userId}}</h2>
        <div class="player">
            <video-player  class="video-player vjs-custom-skin"
                           ref="videoPlayer"
                           :playsinline="true"
                           :options="playerOptions"
                           @play="onPlayerPlay($event)"
                           @pause="onPlayerPause($event)"
            >
            </video-player></div>

        <div style="text-align: center"  id="boxFixed"  :class="{'is_fixed' : isFixed}">
        <video src="http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"
        width="80%" height="auto" controls="" webkit-playsinline="webkit-playsinline"
        playsinline="playsinline" x5-playsinline="x5-playsinline" x-webkit-airplay="allow" preload="a" x5-video-player-type="h5"> </video>
        </div><br><br><br><br><br><br>
        <!--<br><br>-->
        <!--<div style="text-align: center">-->
        <!--<video src="http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4" width="80%" height="auto" controls=""-->
        <!--x5-playsinline="" playsinline="" webkit-playsinline="" poster="../assets/images/background.jpg"-->
        <!--preload="auto"></video>-->
        <!--</div>-->
        <!--<div style="text-align: center">-->
        <!--<video src="http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4" width="80%" height="auto" x5-playsinline=""-->
        <!--playsinline="" webkit-playsinline="" poster="../assets/images/background.jpg" preload></video>-->
        <!--</div>-->
        <!--<br>-->
        <!--<div style="text-align: center">-->
        <!--<video width="80%" height="auto" controls>-->
        <!--<source src="    http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4" type="video/mp4">-->
        <!--</video>-->
        <!--</div>-->
        <!--<br>-->
        <ul>
            <li v-for="(url,index) in videoUrl">
                <div style="text-align: center;position: relative">
                    <span style="position: absolute; top:38%;
            left:50%;
            width:100%;z-index: 1;
            transform:translate(-50%,-50%);color: white;display: block" v-on:click="playPause(index)">观看视频</span>
                    <p class="text" style="position: absolute; top:50%;
            left:50%;
            width:100%;z-index: 1;
            transform:translate(-50%,-50%);color: white;"></p>
                    <video @loadedmetadata="myFunction(index)" class="video" width="80%" height="auto"
                           webkit-playsinline="webkit-playsinline"
                           playsinline="playsinline" x5-playsinline="x5-playsinline" x-webkit-airplay="allow" preload="auto">
                        <source v-bind:src='url.url' type="video/mp4">
                    </video>
                </div>
                <br><br>

            </li>
        </ul>
    </div>
</template>

<script>
    import 'video.js/dist/video-js.css'

    import { videoPlayer } from 'vue-video-player'
    //秒换分秒
    function s_to_hs(S) {
        var h;
        var s;
        h = Math.floor(S / 60);
        s = Math.floor(S % 60);
        //将变量转换为字符串
        h = h+"";
        s = s+"";
        h = (h.length === 1) ? '0' + h : h;
        s = (s.length === 1) ? '0' + s : s;
        return h + "'" + s + '"';
    }

    export default {
        name: "photograph",
        components:{ videoPlayer},
        data() {
            return {
                videoUrl: [
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"},
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"},
                    {url: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4"},
                    {url: "http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4"},
                    {url: "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"}
                ],
                isFixed: false,
                offsetTop:0,
                playerOptions: {
//        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                    autoplay: false, //如果true,浏览器准备好时开始回放。
                    muted: false, // 默认情况下将会消除任何音频。
                    loop: false, // 导致视频一结束就重新开始。
                    preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                    language: 'zh-CN',
                    aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                    fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                    sources: [{
                        type: "video/mp4",
                        src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm" //你的视频地址（必填）
                    }],
                    poster: "", //你的封面地址
                    width: document.documentElement.clientWidth,
                    notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
//        controlBar: {
//          timeDivider: true,
//          durationDisplay: true,
//          remainingTimeDisplay: false,
//          fullscreenToggle: true  //全屏按钮
//        }
                }
            }
        },
        mounted(){
            window.addEventListener('scroll',this.initHeight);
            this.$nextTick( () => {
                this.offsetTop = document.querySelector('#boxFixed').offsetTop;
            })
        },
        methods: {
            onPlayerPlay(player) {

            },
            onPlayerPause(player){

            },

            initHeight () {
                var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                this.isFixed = scrollTop > this.offsetTop ? true : false;
            },

        destroyed () {
            window.removeEventListener('scroll', this.handleScroll)
        },

            playPause: function (x) {
                let video = document.getElementsByClassName("video")[x];
                if (video.paused) {
                    video.play()
                }
                else {
                    video.pause();
                }
                console.log(video.paused)
            },
            myFunction: function (x) {

                    var a = s_to_hs(document.getElementsByClassName("video")[x].duration);
                    var b = document.getElementsByClassName("text")[x];
                    b.innerHTML = a;

            }},
        computed: {
            player() {
                return this.$refs.videoPlayer.player
            }
        }


    }
</script>

<style scoped>
    .box_fixed{
        width: 500px;
        height: 40px;
        border: 2px dashed pink;
        border-radius: 20px;
        margin: 0 auto;
        line-height: 40px;
        background: #eee;
    }
    .is_fixed{
        position: fixed;
        top: 0;
        z-index: 999;
    }

</style>